@import "common.css";
.non-visible {
	display: none;
}
.visible {
	display: block;
}
.container {
	max-width: 1300px;
	margin: 0 auto;
}
.nav {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  width: 100%;
  z-index: 2;
  margin: 0 auto;
	padding-top: 30px;
	padding-bottom: 30px;
	z-index: 2;
}
.nav .container {
		display: flex;
	justify-content: space-between;
}
.nav .container ul {
	display: flex;
}
.nav .container p a {
	font-family: 'Lora', sans-serif;
	font-weight: 700;	
	font-size: 20px;
	color: #F8F9FA;
	}
.nav .container ul li:not(last-child) {
	margin-left: 20px;
}
.nav .container ul li a {
	color: #E5E5E5;
	font-size: 14px;
	padding-bottom: 5px;
}
@media(max-width: 1024px) {
	.container {
		padding-left: 10px;
		padding-right: 10px;
	}
	.nav {
		padding-left: 10px;
		padding-right: 10px;
	}
}
main {
	height: 600px;
/*	background: url('../img/main_back.jpg') 0 0/100% 100% no-repeat;*/
}
.swiper {
	max-width: 100%;
	margin: 0 auto;
	height: 600px;
}
.swiper-slide .container {
	z-index: 1;
	margin-top: 200px;
}
.swiper-slide:first-child {
	background: url('../img/main_back.jpg') 0 0/100% 100% no-repeat;
}
.swiper-slide:nth-child(2) {
	background: url('../img/main_back_2.jpg') 0 0/100% 100% no-repeat;
}
.swiper-slide:last-child {
	background: url('../img/main_back_3.jpg') 0 0/100% 100% no-repeat;
}
.swiper-pagination {
	width: 50px;
	z-index: 1;
}
.swiper-slide .swiper-p {
	height: 20px;
	width: 78px;
	padding: 5px 10px 5px 10px;
	border-radius: 8px;
	background: rgba(255, 255, 255, 0.15);
	font-size: 10px;
	color: white;
}
.bullets__items .swiper-pagination-bullet-active {
	background-color: white;
}
.swiper-slide h1 {
	margin-top: 40px;
	max-width: 500px;
	font-size: 36px;
	font-weight: bold;
	color: white;
}
.swiper-slide div p {
	font-size: 12px;
	color: #E5E5E5;
}
.swiper_date {
	width: 150px;
}
.swiper-slide div p:not(last-child) {
 	margin-right:  0p;
}
.swiper-slide div {
	margin-top: 20px;
	display: flex;
}
main .container {
	display: grid;
	justify-content: left;
}
@media(max-width: 425.9px) {
	.swiper-slide h1 {
		font-size: 28px;
	}
	.swiper-slide div p {
		font-size: 10px;
	}
	.swiper-slide:first-child {
		display: none;
	}
	.swiper-slide:nth-child(2) {
		display: none;
	}
}
@media(max-width: 320.9px) {
	.swiper {
		height: 500px;
	}
	main {
		height: 500px;
	}
}




.topics {
	margin-top: 100px;
}
.topics-nav {
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.topics-nav ul {
	display: flex;
}
.topics-nav h2 {
	font-weight: 700;
	font-size: 36px;
	color: #495057;
}
.topics-nav ul li {
	font-size: 14px;
	color: #495057;
}
.topics-nav ul li:not(last-child) {
	margin-left: 20px;
}
.topics-ul {
	margin-top: 40px;
	display: flex;
	grid-template-columns: 310px 310px 310px 310px 310px 310px 310px 310px;
	flex-wrap: wrap;
	justify-content: center;
}
.li_nav {
	padding-bottom: 5px;
}
.li_nav_active {
	border-bottom: 2px inset yellow;
}
.topics-ul li {
	flex-basis: calc(100% / 4 - 14px);
	margin-right: 7px;
	margin-left: 7px;
	height: 500px;
}
.topic_date {
	font-size: 12px;
	color: #6C757D;
	margin-top: 5px;
}
.topics-ul li h4 {
	font-weight: 700;
	font-size: 18px;
	color: #495057;
	margin-top: 20px;
}

.topic_p {
	color: #6C757D;
	font-size: 12px;
	margin-top: 20px;
}
.topic_category {
	position: absolute;
	top: 5px;
}
@media(max-width:425.9px) {
	.topics-nav ul li {
	font-size: 11px;
	color: #495057;
}
.topics-nav h2 {
	font-weight: 700;
	font-size: 27px;
	color: #495057;
}
}
@media(max-width:375.9px) {
	.topics-nav ul {
	display: grid;
}
	.topics-nav ul li:not(first-child) {
	margin-top: 10px;
}
}


.fashion {
	height: 698px;
	background: url('../img/fasion_back.jpg') 0 0/100% 100% no-repeat;
	display: flex;
  align-items: center;
}
.fashion .container {
	display: flex;
	justify-content: center;
	text-align: center;
	align-items: center;
}
.fashion_block {
	max-width: 538px;
	align-self: center;
}
.fashion .container p:first-child {
	display: inline-block;
	width: 70px;
	color: white;
	flex: none;
   order: 0;
   flex-grow: 0;
   font-size: 10px;
   padding: 5px 5px 5px 5px;
   background-color: rgba(255, 255, 255, 0.3);
   border-radius: 4px;
}
.fashion .container h2 {
	font-size: 36px;
	color: #FFFFFF;
	text-shadow: 1.5px 1.5px 1.5px #000;
	margin-top: 13px;
}
.fashion_block p:nth-child(3) {
	font-size: 12px;
	color: #E5E5E5;
	text-shadow: 1px 1px 1px #000;
	margin-top: 15px;
}
.fashion_block p:last-child {
	color: #FFFFFF;
	font-size: 12px;
	text-shadow: 1px 1px 1px #000;
	margin-top: 20px;
}

@media(max-width: 425.9px) {
	.fashion .container h2 {
		font-size: 28px;
	}
	.fashion .container p {
		font-size: 10px;
	}
}
@media(max-width:320px) {
	.fashion {
		height: 500px;
	}
}



.related-post {
	margin-top: 100px;
}
.related-post .container h2 {
	color: #495057;
	font-size: 36px;
}
.related-post .container ul {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin-top: 40px;
}
.related-post .container ul li {
	width: 420px;
	height: 350px;
	background: url('../img/related_post/1.jpg') 0 0/100% 100% no-repeat;
	margin-left: 7px;
	margin-top: 7px;
	display: flex;
	align-items: flex-end;
}
.related-post .container ul li p {
	font-size: 12px;
	color: #E5E5E5;
}
.related-post .container ul li h4 {
	color: #FFFFFF;
	font-size: 18px;
	margin-top: 20px;
	margin-bottom: 10px;
}
.related_li {
	margin-left: 40px;
	width: 270px;
}
.related_li p:last-child {
	margin-bottom: 40px;
}

@media(max-width: 768.9px) {
	.related-post .container ul li {
		width: 350px;
	height: 300px;
	}
}
@media(max-width: 425.9px) {
		.related-post .container h2 {
			font-size: 28px;
		}
}
@media(max-width: 375.9px) {
	.related-post .container ul li {
		width: 100%;
		margin-left: 0;
	}
}
footer {
	margin-top: 100px;
	width: 100%;
	height: 80px;
	background: #343A40;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding-left: 70px;
	padding-right: 70px;
}
footer p {
	color: #E5E5E5;
	font-size: 14px;
}
footer a {
	color: #E5E5E5;
	font-size: 14px;
}